import {Component} from "react";
import Post from "./PostComponent";
import * as NasaAPI from '../utils/NasaAPI'
class Home extends Component{
    state = {
        imgs: []
    }
    finishLoad(){
        const loading = document.getElementById("loading")
        if(loading){
            loading.remove();            
        }
    }
    componentDidMount(){
        let loader = document.createElement('div')
        let main = document.getElementById('main')
        main.appendChild(loader)
        loader.id = "loading"
        NasaAPI.getApod()
        .then(res => {
            const data = res.map((item) => {
                const post = {
                    user: "NasaAPOD",
                    url: item.url,
                    date: item.date,
                    desc: item.explanation,
                    type: item.media_type
                }
                return post
            })
            return data.reverse()
        })
        .then(data => this.setState({
            imgs: data
        })).then(() => {
            this.finishLoad()
        })
    }
    render(){
        const array = this.state.imgs
        return(
            <div>
                {array.slice(0,10).map((nuum) => {
                    return (
                        <Post 
                            data = {nuum}
                        />
                    )
                })   
                }
          </div>
        )
    }
}

export default Home