import {Component} from 'react'
import '../styles/profile.css'
import * as NasaAPI from '../utils/NasaAPI'
class Profile extends Component{
    state = {
        imgs: [],
        username: this.props.user
    }
    componentDidMount(){
            let loader = document.createElement('div')
            let posts = document.getElementById('profile-posts')
            posts.appendChild(loader)
            loader.id = "loading"
                NasaAPI.getApod()
                .then(res => {
                    const data = res.map((item) => {
                        const post = {
                            user: "NasaAPOD",
                            url: item.url,
                            date: item.date,
                            desc: item.explanation,
                            type: item.media_type
                        }
                        return post
                    })
                    return data.reverse()
                })
                .then(data => this.setState({
                    imgs: data
                })).then(() => {
                    const loading = document.getElementById("loading")
                    if(loading){
                        loading.remove();            
                    }
                })    
    }
    render(){
        const arr = this.state.imgs
        return(
            <div className="profile-body">
                <div className="profile-header">
                    <div className="profile-picture"></div>
                    <div>
                        <h1>{this.state.username}</h1>
                        <h2>Astronomy Photo of the Day</h2>                    
                    </div>                    
                </div>
                <h3 className="profile-posts-title" id="profile-posts">Posts</h3>
                <div className="profile-posts" >
                    {arr.map((item) => {
                        return(
                            <div className="profile-post-item" style={{backgroundImage: `url(${item.url})`}}></div>
                        )
                    })}
                </div>
            </div>
        )}
}

export default Profile