import { Component } from "react";
import '../styles/header.css'
class Header extends Component{
    state = {
        date: "September 15th 2021",
        user: "Herman Woo",
        displayPic: "./imgs/default.jpg"
      }
    componentDidMount(){
        const today = new Date()
        const numDate = today.toString().slice(8,10)
        const suffix = () => {
          let value = ""
          if (numDate.slice(-1) === "1"){
            if(numDate.slice(-2) === "1"){
              value = "th"
            }
            else{
              value = "st"
            }
          }
          else if (numDate.slice(-1) === "2"){
            value = "nd"
          }
          else if (numDate.slice(-1) === "3"){
            value = "rd"
          }
          else{
            value = "th"
          }
          return value+" "
        }
        const d = today.toString().slice(4,7)+" "+numDate+suffix()+" "+today.toString().slice(11,15)
        this.setState({
          date: d
        })
      }
    render(){
        return(
            <header>
                <div className="app-logo header-item">
                    <h1>
                        Spacestagram
                    </h1>
                    <h5>
                        Image-sharing from the Final Frontier
                    </h5>
                </div>
                <div className="app-date header-item">
                    <h2>
                        {this.state.date}     
                    </h2>
                </div>
                <a className="app-user header-item header-link">
                    <img className="app-user-image"></img>
                    <h3 className="app-user-name">{this.state.user}</h3>
                </a>
          </header>
        )
    }
}

export default Header